.about__img {
    max-width: 40%;
}

.about__container {
    grid-template-columns: 3fr 9fr;
    column-gap: 1.875rem;
}

.about__data {
    padding: 1.875rem;
    background-color: var(--container-color);
    box-shadow: var(--shadow);
    border-radius: var(--border-radius);
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1.875rem;
    align-items: flex-start;
    position: relative;
}

.about__data::before {
    content: '';
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 15px solid var(--container-color);
    position: absolute;
    left: -0.93rem;
    top: 20%;
}

.about__description {
    margin-bottom: 1rem;
    text-align: justify;
}

.about__cv {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
    gap: 1rem;
}

.about__cv .btn {
    flex: 1;
    text-align: center;
    font-size: var(--smaller-font-size);
}

.about__skills {
    row-gap: 1.25rem;
}

.about_skills__titles {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
}

.about_skills__name {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
}

.about_skills__number {
    line-height: 1.2;
}

.about_skills__bar,
.about_skills__percentage {
    height: 7px;
    border-radius: 0.25rem;
}

.about_skills__bar {
    background-color: #b6b2b279;
}

.about_skills__percentage {
    display: block;
}

.frontend {
    width: 80%;
    background-color: rgb(205, 129, 255);
}

.backend {
    width: 70%;
    background-color: rgb(255, 129, 249);
}

.database {
    width: 50%;
    background-color: rgb(255, 129, 135);
}

.unit {
    width: 60%;
    background-color: rgb(250, 183, 121);
}

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 500px;
        justify-content: center;
        row-gap: 1.875rem;
    }

    .about__data::before {
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 10px solid #fff;
        left: 49%;
        top: -20px;
    }

    .about__img {
        justify-self: center;
    }
}

@media screen and (max-width: 768px) {
    .about__container {
        grid-template-columns: 400px;
    }

    .about__data::before {
        left: 47%;
    }

    .about__data {
        grid-template-columns: 1fr;
        row-gap: 1.875rem;
    }

    .about__img {
        justify-self: center;
    }
}

@media screen and (max-width: 576px) {
    .about__container {
        grid-template-columns: 320px;
    }
}

@media screen and (max-width: 350px) {
    .about__container {
        grid-template-columns: 1fr;
    }
}